<template>
  <el-row class="login-wrapper" type="flex" justify="center" align="middle">
    <div class="login">
      <el-form :model="formLogin" label-position="left">
        <el-form-item class="title">
          <!-- <h2>Vue</h2> -->
          <h2>RAP</h2>
        </el-form-item>
        <el-form-item>
          <el-input
            ref="account"
            v-model="formLogin.loginName"
            placeholder=""
            @keyup.enter.native="login"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-input
            v-model="formLogin.password"
            placeholder=""
            type="password"
            @keyup.enter.native="login"
          ></el-input>
        </el-form-item>
        <el-row type="flex" justify="center">
          <el-button
            type="primary"
            @click="login"
            :disabled="!formLogin.loginName || !formLogin.password"
          ></el-button>
        </el-row>
      </el-form>
    </div>
    <!-- <p class="bottom-text"></p> -->
  </el-row>
</template>

<script>
import { constants } from 'crypto'
export default {
  name: 'login',
  data() {
    return {
      formLogin: {
        password: '',
        loginName: ''
      },
      userList:{}
    }
  },
  mounted() {
    this.$refs.account.focus()
    this.api.getAllClient().then((res)=>{
      // console.log(res)
      res.forEach(element => {
        this.userList[element.name]=element
      })
    })
  },
  methods: {
    login() {
      
      if(this.userList[this.formLogin.loginName]){
        if(this.userList[this.formLogin.loginName].password===this.formLogin.password){
          this.$message({
            message: '',
            type: 'success'
          })
          console.log(this.userList[this.formLogin.loginName])
          this.$store.commit('set_userInfo',this.userList[this.formLogin.loginName])
          this.$router.replace({ path: '/main' })
        }else{
          this.$message.error('')

        }
      }else{
        this.$message.error('')

      }

      // let tempData = {
      //   name: this.formLogin.loginName,
      //   password: this.formLogin.password
      // }
      // this.$store
      //   .dispatch('login', tempData)
      //   .then(() => {
      //     this.$message.success('')
      //     this.$router.replace({ path: '/' })
      //   })
      //   .catch(err => {
      //     this.$message.error(err)
      //   })
    }
  }
}
</script>

<style lang="scss" scoped>
.login-wrapper {
  height: 100%;
  background: #2d3a4b;
  .login {
    width: 30%;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    padding: 2% 4%;
    box-sizing: border-box;
    .title {
      text-align: center;
      color: #eee;
    }
    .el-button {
      width: 40%;
    }
  }
}
.bottom-text {
  position: absolute;
  bottom: 5%;
  color: #eee;
}
</style>